<template>
	<div id="app">
		<div class="hello">
			<div class="container" :style="{transform:`scale(`+xscale+`,`+yscale+`)`}" style="transform-origin: 0 0;">
				<div class="titleView">
					<div class="dateView">
						<p class="dateTxt">{{nowDate}}</p>
						<span>|</span>
						<p>{{weather}}</p>
					</div>
					<p class="title">唐山南湖旅游景区</p>
				</div>

				<div class="content">
					<div class="leftView">
						<dv-border-box-12>
							<div class="boxView">
								<p class="topTitle">自行车运营情况</p>
								<div class="zxcyy">
									<dv-charts :option="zxcyyLine" style="width: 400px;height: 200px;" />
									<div class="syl">
										<p>使用率</p>
										<dv-water-level-pond :config="zxcyyWater" style="width:100px;height:100px" />
									</div>
								</div>
							</div>
						</dv-border-box-12>

						<dv-border-box-12>
							<div class="boxView">
								<p class="topTitle">电瓶车运营情况</p>
								<div class="dpcyy">
									<dv-charts :option="ggcyyLine" style="width: 100%;height: 200px;" />
									<div class="scrs">
										<p>上车人数</p>
										<dv-charts :option="dpcBar" style="width: 100%;height: 170px;" />
									</div>
								</div>
							</div>
						</dv-border-box-12>

						<dv-border-box-12>
							<div class="boxView">
								<p class="topTitle">自驾船运营情况</p>
								<div class="zxcyy">
									<dv-charts :option="shipyyLine" style="width: 400px;height: 200px;" />
									<div class="syl">
										<p>使用率</p>
										<dv-water-level-pond :config="shipWater" style="width:100px;height:100px" />
									</div>
								</div>
							</div>
						</dv-border-box-12>
					</div>

					<div class="centerView">
						<dv-border-box-12>
							<div class="mapView">
								<iframe src="http://smart-ideas.com.cn/mapView/nhmap.html" frameborder="0" width="100%" height="100%" style="border-radius: 10px;"></iframe>
							</div>
						</dv-border-box-12>
						
						<div class="zxcdd">
							<dv-border-box-12>
								<div class="boxView">
									<p class="topTitle">自行车订单统计</p>
									<div class="zxcyy">
										<div style="width: 100%;">
											<div class="searchView">
												<input id="startDate" type="text" readonly="readonly" v-model="startDate">
												至
												<input id="endDate" type="text" readonly="readonly" v-model="endDate">
												<a href="javascript:;" @click="searchClick()">查询</a>
											</div>
											
											<div class="searchResult">
												<div class="resultItem">
													<p>自行车收入情况</p>
													<div class="chartView">
														<dv-charts :option="srLine" style="width: 350px;height: 250px;" />
														<dv-active-ring-chart :config="srPie" style="width:250px;height:250px" />
													</div>
												</div>
												<div class="resultItem">
													<p>自行车订单情况</p>
													<div class="chartView">
														<dv-charts :option="ddLine" style="width: 350px;height: 250px;" />
														<dv-active-ring-chart :config="ddPie" style="width:250px;height:250px" />
													</div>
												</div>
											</div>
										</div>
									</div>
								</div>
							</dv-border-box-12>
						</div>
					</div>

					<div class="rightView">
						<dv-border-box-12>
							<div class="boxView">
								<p class="topTitle">自行车使用详情</p>
								<div class="dpcyy">
									<dv-charts :option="zxcDetail" style="width: 100%;height: 152px;" />
								</div>
							</div>
						</dv-border-box-12>

						<dv-border-box-12>
							<div class="boxView">
								<p class="topTitle">自驾船使用详情</p>
								<div class="dpcyy">
									<dv-charts :option="zjcDetail" style="width: 100%;height: 152px;" />
								</div>
							</div>
						</dv-border-box-12>

						<dv-border-box-12>
							<div class="boxView">
								<p class="topTitle">观光车使用详情</p>
								<div class="dpcyy">
									<dv-charts :option="ggcDetail" style="width: 100%;height: 151px;" />
								</div>
							</div>
						</dv-border-box-12>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import laydate from 'layui-laydate';
	import bikeIcon from './assets/bike-icon.png';
	import carIcon from './assets/dpcIcon.png';
	import shipIcon from './assets/mark1.png';
	import zdIcon from './assets/zd.png';
	export default {
		data() {
			return {
				shipWater: {},
				weather: '',
				nowDate: '',
				xscale: 1,
				yscale: 1,
				lastDate: 0,
				startDate: '',
				endDate: '',
				zxcyyLine: {},
				zxcyyWater: {},
				shipyyLine: {},
				shipyyWater: {},
				ggcyyLine: {},
				dpcBar: {},
				zxcDetail: {},
				zjcDetail: {},
				ggcDetail: {},
				searchStart: '',
				searchEnd: '',
				srPie: {},
				ddPie: {},
				srLine: {},
				ddLine: {}
			}
		},
		methods: {
			searchClick: function(){
				let that = this;
				that.searchStart = that.startDate;
				that.searchEnd = that.endDate;
				that.getBikeIncome();
			},
			getBikeIncome: function(){
				console.log('1212');
				let that = this;
				let startDate = that.searchStart;
				let endDate = that.searchEnd;
				axios.get('http://lease.smart-ideas.com.cn/nhpark/thirdPartyDataView/getDeviceLeaseDetails?startDate=' + startDate + '&endDate=' + endDate)
					.then(function(res) {
						let twoIncome = 0;
						let fourIncome = 0;
						let twoTotal = 0;
						let fourTotal = 0;
						res.data.forEach(function(value, key){
							if(value.deviceType == '1'){
								twoIncome = value.income;
								twoTotal = value.quantity;
							}
							
							if(value.deviceType == '2'){
								fourIncome = value.income;
								fourTotal = value.quantity;
							}
						})
						that.srPie = {
							color: ['blue', 'yellow'],
							data: [
								{
								  name: '双人自行车',
								  value: twoIncome
								},
								{
								  name: '四人自行车',
								  value: fourIncome
								}
							],
							lineWidth: 15,
							digitalFlopStyle: {
							  fontSize: 16,
							  fill: '#fff'
							}
						}
						
						that.ddPie = {
							color: ['blue', 'yellow'],
							data: [
								{
								  name: '双人自行车',
								  value: twoTotal
								},
								{
								  name: '四人自行车',
								  value: fourTotal
								}
							],
							lineWidth: 15,
							digitalFlopStyle: {
							  fontSize: 16,
							  fill: '#fff'
							}
						}
					}).catch(function(err) {
						console.log(err);
					})
					
				axios.get('http://lease.smart-ideas.com.cn/nhpark/thirdPartyDataView/getDeviceLeaseGroup?startDate=' + startDate + '&endDate=' + endDate)
					.then(function(res) {
						console.log(res);
						let incomeList = res.data;
						let timeList = [];
						let moneyList = [];
						let numberList = [];
						incomeList.forEach(function(value, key){
							let time = value.date.split('-')[1] + '-' + value.date.split('-')[2];
							let totalNumber = value.total;
							let income = value.income;
							timeList.push(time);
							moneyList.push(income);
							numberList.push(totalNumber);
							that.srLine = {
								xAxis: {
									data: timeList,
									axisLine: {
										style: {
											stroke: '#fff',
											lineWidth: 1
										}
									},
									axisLabel: {
										style: {
											fill: '#fff',
											fontSize: 10,
											rotate: 0
										}
									}
								},
								yAxis: {
									min: 0,
									data: 'value',
									axisLine: {
										style: {
											stroke: '#fff',
											lineWidth: 1
										}
									},
									splitLine: {
										show: false
									},
									axisLabel: {
										style: {
											fill: '#fff',
											fontSize: 10,
											rotate: 0
										}
									}
								},
								series: [{
									data: moneyList,
									type: 'line',
									lineArea: {
										show: true
									},
									label: {
										show: true,
										formatter: '{value}',
										style: {
											fill: '#fff'
										}
									}
								}],
								grid: {
									top: '5',
									left: '10',
									right: '0',
									bottom: '15'
								}
							}
							
							that.ddLine = {
								xAxis: {
									data: timeList,
									axisLine: {
										style: {
											stroke: '#fff',
											lineWidth: 1
										}
									},
									axisLabel: {
										style: {
											fill: '#fff',
											fontSize: 10,
											rotate: 0
										}
									}
								},
								yAxis: {
									min: 0,
									data: 'value',
									axisLine: {
										style: {
											stroke: '#fff',
											lineWidth: 1
										}
									},
									splitLine: {
										show: false
									},
									axisLabel: {
										style: {
											fill: '#fff',
											fontSize: 10,
											rotate: 0
										}
									}
								},
								series: [{
									data: numberList,
									type: 'line',
									lineArea: {
										show: true
									},
									label: {
										show: true,
										formatter: '{value}',
										style: {
											fill: '#fff'
										}
									}
								}],
								grid: {
									top: '5',
									left: '10',
									right: '0',
									bottom: '15'
								}
							}
						})
						
					}).catch(function(err) {
						console.log(err);
					})
			}
		},
		mounted: function() {
			let that = this;
			let xScale = window.innerWidth / 1920;
			let yScale = window.innerHeight / 1080;
			that.xscale = xScale;
			that.yscale = yScale;
			let nowDate = new Date();
			let nowYear = nowDate.getFullYear();
			let nowMonth = nowDate.getMonth() + 1;
			nowMonth = nowMonth > 9 ? nowMonth : '0' + nowMonth;
			let nowDay = nowDate.getDate();
			nowDay = nowDay > 9 ? nowDay : '0' + nowDay;
			that.startDate = nowYear + '-' + nowMonth + '-' + nowDay;
			that.endDate = nowYear + '-' + nowMonth + '-' + nowDay;
			that.searchStart = nowYear + '-' + nowMonth + '-' + nowDay;
			that.searchEnd = nowYear + '-' + nowMonth + '-' + nowDay;
			
			laydate.render({
				elem: '#startDate',
				done: function(value, date) {
					that.startDate = value;
					that.searchStart = value;
				}
			});
			
			laydate.render({
				elem: '#endDate',
				done: function(value, date) {
					that.endDate = value;
					that.searchEnd = value;
				}
			});

			setInterval(function() {
				var nowYear = new Date().getFullYear();
				var nowMonth = new Date().getMonth() + 1;
				nowMonth = nowMonth > 9 ? nowMonth : '0' + nowMonth;
				var nowDay = new Date().getDate();
				nowDay = nowDay > 9 ? nowDay : '0' + nowDay;
				var nowHour = new Date().getHours();
				nowHour = nowHour > 9 ? nowHour : '0' + nowHour;
				var nowMinutes = new Date().getMinutes();
				nowMinutes = nowMinutes > 9 ? nowMinutes : '0' + nowMinutes;
				var nowSeconds = new Date().getSeconds();
				nowSeconds = nowSeconds > 9 ? nowSeconds : '0' + nowSeconds;
				var nowWeek = new Date().getDay();
				if (nowWeek == 0) {
					nowWeek = '星期日';
				} else if (nowWeek == 1) {
					nowWeek = '星期一';
				} else if (nowWeek == 2) {
					nowWeek = '星期二';
				} else if (nowWeek == 3) {
					nowWeek = '星期三';
				} else if (nowWeek == 4) {
					nowWeek = '星期四';
				} else if (nowWeek == 5) {
					nowWeek = '星期五';
				} else if (nowWeek == 6) {
					nowWeek = '星期六';
				}
				that.nowDate = nowYear + '-' + nowMonth + '-' + nowDay + ' ' + nowWeek + ' ' + nowHour + ':' + nowMinutes + ':' +
					nowSeconds;

				// if(that.lastDate == '0'){
				// 	that.lastDate = getLastDate(nowYear + '-' + nowMonth + '-' + nowDay, -1);
				// }
			}, 1000)

			//获取天气情况
			axios.get('http://node.smart-ideas.com.cn:3001/datav/common/weather/v1/weatherInfo?city=320100')
				.then(function(res) {
					that.weatherList = res.data;
					that.weather = res.data[0].temperature + ' ' + res.data[0].weather;
				}).catch(function(err) {
					console.log(err);
				})

			setInterval(function() {
				getData();
				that.getBikeIncome();
			}, 20000)

			getData();
			that.getBikeIncome();

			function getData() {
				// 自行车运营情况
				axios.get('http://lease.smart-ideas.com.cn/nhpark/operation/getDeviceHourIncome?type=bike')
					.then(function(res){
						let dataList = res.data;
						let nameList = [];
						let valueList = [];
						dataList.forEach(function(value, key){
							nameList.push(value.time);
							valueList.push(parseFloat(value.income));
						})
						that.zxcyyLine = {
							xAxis: {
								data: nameList,
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								splitLine: {
									show: false
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								}
							},
							series: [{
								data: valueList,
								type: 'line',
								lineArea: {
									show: true
								}
							}],
							grid: {
								top: '5',
								left: '10',
								right: '0',
								bottom: '15'
							}
						}
					})
					.catch(function(err){
						console.log(err);
					})
					
				// 自驾船运营情况
				axios.get('http://lease.smart-ideas.com.cn/nhpark/operation/getDeviceHourIncome?type=ship')
					.then(function(res){
						let dataList = res.data;
						let nameList = [];
						let valueList = [];
						dataList.forEach(function(value, key){
							nameList.push(value.time);
							valueList.push(parseFloat(value.income));
						})
						that.shipyyLine = {
							xAxis: {
								data: nameList,
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								splitLine: {
									show: false
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								}
							},
							series: [{
								data: valueList,
								type: 'line',
								lineArea: {
									show: true
								}
							}],
							grid: {
								top: '5',
								left: '10',
								right: '0',
								bottom: '15'
							}
						}
					})
					.catch(function(err){
						console.log(err);
					})
					
				// 观光车运营情况
				axios.get('http://lease.smart-ideas.com.cn/nhpark/operation/getDeviceHourIncome?type=loop')
					.then(function(res){
						let dataList = res.data;
						let nameList = [];
						let valueList = [];
						dataList.forEach(function(value, key){
							nameList.push(value.time);
							valueList.push(parseFloat(value.income));
						})
						that.ggcyyLine = {
							xAxis: {
								data: nameList,
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								},
								splitLine: {
									show: false
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								}
							},
							series: [{
								data: valueList,
								type: 'line',
								lineArea: {
									show: true
								}
							}],
							grid: {
								top: '5',
								left: '10',
								right: '0',
								bottom: '15'
							}
						}
					})
					.catch(function(err){
						console.log(err);
					})
					
				// 自行车自驾船使用率
				axios.get('http://lease.smart-ideas.com.cn/nhpark/operation/getDeviceUsage')
					.then(function(res) {
						let bikeTotal = res.data[0].bikeTotal;
						let bikeUseTotal = res.data[0].bikeUseTotal;
						let shipTotal = res.data[0].shipTotal;
						let shipUseTotal = res.data[0].shipUseTotal;
						let bikePercent = Math.ceil(bikeUseTotal / bikeTotal * 100);
						let shipPercent = Math.ceil(shipUseTotal / shipTotal * 100);
						that.zxcyyWater = {
							colors: ['#40ea2e', '#40ea2e', '#40ea2e'],
							waveHeight: 10,
							waveNum: 3,
							waveOpacity: 0.2,
							data: [bikePercent]
						}
						that.shipWater = {
							colors: ['#40ea2e', '#40ea2e', '#40ea2e'],
							waveHeight: 10,
							waveNum: 3,
							waveOpacity: 0.2,
							data: [shipPercent]
						}
					})
					.catch(function(err){
						console.log(err);
					})
					
				// 观光车上车人数
				axios.get('http://lease.smart-ideas.com.cn/nhpark/thirdPartyDataView/getDeviceSiteCheck')
					.then(function(res) {
						let dataList = res.data;
						let nameList = [];
						let valueList = [];
						dataList.forEach(function(value, key){
							nameList.push(value.siteName);
							valueList.push(parseInt(value.checkTotal));
						})
						that.dpcBar = {
							color: ['yellow'],
							xAxis: {
								data: nameList,
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 25
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								axisLabel: {
									show: false
								},
								splitLine: {
									show: false
								}
							},
							series: [{
								data: valueList,
								type: 'bar',
								label: {
									show: true,
									position: 'top',
									offset: [0, -5],
									style: {
									  fill: '#fff'
									}
								}
							}]
						}
					})
					.catch(function(err){
						console.log(err);
					})
				
				
				// 自行车使用详情
				axios.get('http://lease.smart-ideas.com.cn/nhpark/thirdPartyDataView/getDeviceSiteLeaseDetail?type=bike')
					.then(function(res) {
						let dataList = res.data;
						let nameList = [];
						let leaseList = [];
						let noLeaseList = [];
						dataList.forEach(function(value, key) {
							nameList.push(value.siteName);
							leaseList.push(parseInt(value.leaseTotal));
							noLeaseList.push(parseInt(value.noLeaseTotal));
						})
						that.zxcDetail = {
							color: ['blue', 'yellow'],
							legend: {
								data: ['已租赁', '未租赁'],
								bottom: 10,
								textStyle: {
									fill: '#fff',
									fontSize: 11
								}
							},
							xAxis: {
								data: nameList,
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								splitLine: {
									show: false
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							series: [
								{
									name: '已租赁',
									data: leaseList,
									type: 'bar',
									label: {
										show: true,
										position: 'top',
										offset: [0, -5],
										style: {
										  fill: '#fff'
										}
									}
								},
								{
									name: '未租赁',
									data: noLeaseList,
									type: 'bar',
									label: {
										show: true,
										position: 'top',
										offset: [0, -6],
										style: {
										  fill: '#fff',
										  fontSize: 12
										}
									}
								}
							],
							grid: {
								top: '5',
								left: '10',
								right: '5',
								bottom: '35'
							}
						}
					}).catch(function(err) {
						console.log(err);
					})
					
				// 自驾船使用详情
				axios.get('http://lease.smart-ideas.com.cn/nhpark/operation/getDeviceShipLeaseDetail')
					.then(function(res) {
						let dataList = res.data;
						let nameList = [];
						let leaseList = [];
						let noLeaseList = [];
						dataList.forEach(function(value, key) {
							let total = parseInt(value.total);
							let leaseNumber = parseInt(value.useTotal);
							let noLeaseNumber = total*1 - leaseNumber*1;
							nameList.push(value.shipName);
							leaseList.push(leaseNumber);
							noLeaseList.push(noLeaseNumber);
						})
						that.zjcDetail = {
							color: ['blue', 'yellow'],
							legend: {
								data: ['已租赁', '未租赁'],
								bottom: 10,
								textStyle: {
									fill: '#fff',
									fontSize: 11
								}
							},
							xAxis: {
								data: nameList,
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								splitLine: {
									show: false
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							series: [
								{
									name: '已租赁',
									data: leaseList,
									type: 'bar',
									label: {
										show: true,
										position: 'top',
										offset: [0, -6],
										style: {
										  fill: '#fff'
										}
									}
								},
								{
									name: '未租赁',
									data: noLeaseList,
									type: 'bar',
									label: {
										show: true,
										position: 'top',
										offset: [0, -6],
										style: {
										  fill: '#fff',
										  fontSize: 12
										}
									}
								}
							],
							grid: {
								top: '5',
								left: '10',
								right: '5',
								bottom: '35'
							}
						}
					}).catch(function(err) {
						console.log(err);
					})
					
				// 观光车使用详情
				axios.get('http://lease.smart-ideas.com.cn/nhpark/operation/getLineBatteryQuantity')
					.then(function(res) {
						let dataList = res.data;
						let nameList = [];
						let useList = [];
						dataList.forEach(function(value, key) {
							let useNumber = value.total;
							useNumber = useNumber?useNumber:0;
							nameList.push(value.lineName);
							useList.push(parseInt(useNumber));
						})
						that.ggcDetail = {
							color: ['yellow'],
							legend: {
								data: ['运行车辆'],
								bottom: 10,
								textStyle: {
									fill: '#fff',
									fontSize: 11
								}
							},
							xAxis: {
								data: nameList,
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							yAxis: {
								min: 0,
								data: 'value',
								splitLine: {
									show: false
								},
								axisLabel: {
									style: {
										fill: '#fff',
										fontSize: 10,
										rotate: 0
									}
								},
								axisLine: {
									style: {
										stroke: '#fff',
										lineWidth: 1
									}
								}
							},
							series: [
								{
									name: '运行车辆',
									data: useList,
									type: 'bar',
									barWidth: 25,
									label: {
										show: true,
										position: 'top',
										offset: [0, -6],
										style: {
										  fill: '#fff',
										  fontSize: 12
										}
									}
								}
							],
							grid: {
								top: '5',
								left: '10',
								right: '5',
								bottom: '35'
							}
						}
					}).catch(function(err) {
						console.log(err);
					})
			}
		}
	}
</script>

<style>
	* {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
		font-family: "Microsoft Yahei";
	}



	img {
		display: block;
	}

	.hello {
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		background: url(../src/assets/bg.png) no-repeat left top;
		background-size: 100% 100%;
	}

	.container {
		width: 1920px;
		height: 1080px;
		background-color: rgba(54, 64, 71, .7);
	}

	.container .dv-scroll-board .rows .ceil {
		white-space: normal;
	}

	.container .dv-scroll-board .rows .ceil p {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.container .dv-scroll-board .header .header-item {
		font-size: 12px;
	}


	/* 标题 */
	.titleView {
		width: 100%;
		position: relative;
		padding: 10px 0;
		background: url(assets/titleBg.png) no-repeat left center;
		background-size: 100% 100%;
	}

	.titleView .title {
		letter-spacing: 5px;
		width: 100%;
		line-height: 65px;
		text-align: center;
		opacity: 1;
		width: 100%;
		height: 65px;
		padding-left: 20px;
		font-family: "Microsoft Yahei";
		font-size: 32px;
		color: rgb(255, 255, 255);
		letter-spacing: 0px;
		filter: drop-shadow(rgb(0, 117, 255) 0px 0px 8px);
	}



	.dateView {
		position: absolute;
		right: 20px;
		top: 0;
		color: #fff;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
		line-height: 60px;
	}

	.dateTxt {
		width: 185px;
	}

	.dateView span {
		padding: 0 10px;
		font-size: 12px;
	}


	.content {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		padding: 10px 20px 20px;
		position: relative;
	}

	.leftView {
		/* width: 500px; */
	}

	.topTitle {
		width: 100%;
		font-size: 14px;
		color: #7491e8;
		line-height: 35px;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
		padding-left: 30px;
		background: url(assets/title-icon.png) no-repeat 7px #122374;
		background-size: 16px;
		letter-spacing: 2px;
	}

	.boxView {
		padding: 5px;
		overflow: hidden;
	}

	/* 自行车运营情况 */
	.zxcyy {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		padding: 10px;
	}

	.syl {
		width: 200px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.syl p {
		line-height: 60px;
		text-align: center;
		width: 100%;
		color: #fff;
		font-size: 14px;
	}

	/* 电瓶车运营情况 */
	.scrs p {
		font-size: 14px;
		color: #fff;
		text-align: left;
		line-height: 30px;
		padding-left: 30px;
	}

	/* 地图部分 */
	.centerView{
		position: relative;
	}
	.mapView {
		padding: 6px;
		width: 700px;
		height: 590px;
	}

	.mapView #myMap {
		width: 100%;
		height: 100%;
		border-radius: 10px;
	}
	
	/* 自行车订单统计 */
	.zxcdd{
		width: 1250px;
		position: absolute;
		height: 385px;
	}

	/* 右侧部分 */
	.rightView {
		width: 550px;
	}
	
	.searchView{
		display: flex;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
		color: #fff;
	}
	.searchView input{
		width: 100px;
		height: 35px;
		font-size: 14px;
		color: #333;
		margin: 0 10px;
	}
	.searchView a{
		display: block;
		width: 100px;
		line-height: 35px;
		background-color: #007AFF;
		color: #fff;
		font-size: 14px;
		text-align: center;
		text-decoration: none;
		border-radius: 5px;
	}
	.searchResult{
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		width: 100%;
	}
	.resultItem{
		width: 50%;
		font-size: 14px;
		color: #fff;
		padding: 10px;
	}
	.chartView{
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
</style>



